// APIURL
// const BASE_URL = 'http://localhost:8090'; // PocketBase8090
const BASE_URL = 'http://www.word.heluobo.top'; // PocketBase8090

// 
const request = (url, method, data = null) => {
  return new Promise((resolve, reject) => {
    // token
    const token = uni.getStorageSync('token') || '';

    // 
    const options = {
      url: url,
      method: method,
      header: {
        'Content-Type': 'application/json',
      },
      success: (res) => {
        resolve(res);
      },
      fail: (err) => {
        console.error('API:', err);
        reject(err);
      }
    };

    // token
    if (token) {
      options.header.Authorization = `Bearer ${token}`;
    }

    // 
    if (data) {
      options.data = data;
    }

    // 
    uni.request(options);
  });
};

// API
export default {
  // 
  login(code, userInfo) {
    return request(
      `${BASE_URL}/api/wechat/login`,
      'POST',
      { code, userInfo }
    );
  },

  // 
  getUserInfo(userId) {
    return request(
      `${BASE_URL}/api/collections/users/records/${userId}`,
      'GET'
    );
  },

  // 
  syncUserData(userId, data) {
    return request(
      `${BASE_URL}/api/collections/user_data/records`,
      'POST',
      {
        user: userId,
        data: JSON.stringify(data),
        updated: new Date().toISOString()
      }
    );
  },

  // 
  getUserData(userId) {
    return request(
      `${BASE_URL}/api/collections/user_data/records?filter=(user='${userId}')&sort=-updated&limit=1`,
      'GET'
    );
  },

  // 
  favoriteWord(userId, wordId, isFavorite) {
    return request(
      `${BASE_URL}/api/collections/favorites/records`,
      'POST',
      {
        user: userId,
        word: wordId,
        isFavorite: isFavorite,
        created: new Date().toISOString()
      }
    );
  },

  // 
  getFavorites(userId) {
    return request(
      `${BASE_URL}/api/collections/favorites/records?filter=(user='${userId}'%26%26isFavorite=true)&expand=word`,
      'GET'
    );
  }
};
